.centrar-content{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 300px;
}

.centrar-content-vertical{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 300px;

}

.centrar-text{
  text-align: center;
}


